   /*}*/
body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1rem;
  line-height: 1.58;
  color: #333;
  /*background-color: #f4f4f4;*/
}
body:before {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  /*background: #128ff2;*/
  content: "";
  z-index: 0;
}
.clearfix:after {
  display: block;
  content: "";
  clear: both;
}
h1, h2, h3, h4, h5, h6 {
  margin-top: 20px;
  margin-bottom: 20px;
}
h1 {
  font-size: 1.7em;
}
a {
  color: #128ff2;
}
button {
  -webkit-box-shadow: none;
          box-shadow: none;
  border: 1px solid transparent;
  font-size: 14px;
  outline: none;
  line-height: 100%;
  white-space: nowrap;
  vertical-align: middle;
  padding: 0.6rem 1rem;
  border-radius: 2px;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  min-height: 38px;
}
button.primary {
  background-color: #128ff2;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.12);
          box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.12);
  color: #fff;
}
input {
  font-size: 1rem;
}
input[type="file"] {
  border: 1px solid #128ff2;
  padding: 6px;
  max-width: 100%;
}
.file-input {
  width: 100%;
}
.submit-btn {
  display: block;
  margin-top: 15px;
  min-width: 100px;
}
@media screen and (min-width: 500px) {
.file-input {
    width: calc(100% - 115px);
}
.submit-btn {
    display: inline-block;
    margin-top: 0;
    margin-left: 10px;
}
}
.upload-container {
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;
  background-color: #fff;
  -webkit-box-shadow: 0 1px 11px rgba(0, 0, 0, 0.27);
          box-shadow: 0 1px 11px rgba(0, 0, 0, 0.27);
  margin-top: 2%;
  min-height: 400px;
  position: relative;
  padding: 20px;
}
.upload-header {
  border-bottom: 1px solid #ececec;
}
.upload-header h2 {
  font-weight: 500;
}
.single-upload {
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #e8e8e8;
}


